import React from "react";
import {Box, Container, Grid, Button} from "@material-ui/core";
import "./HomePageBody.css";
import fakeThoughts from "../../FakeData/FakeThoughts";
import ThoughtForm from "../Thoughtform/ThoughtForm";
import ThoughtButton from "../Thoughtform/ThoughtButton";
import TutorialModal from "../Tutorial/TutorialModal";

export default function HomePageBody() {

    const formattedThought = fakeThoughts.map((thought, index) => (
        <Grid item xs={12} sm={12} md={4} key={index}>
            <Box
                sx={{
                    margin: "20px",
                    height: "200px",
                    bgcolor: "white",
                    border: "3px solid gray",
                    borderRadius: "1.5em",
                }}
                data-testid="preview-box"
            >
                <div data-testid="first-thought">
                    first Thought: {thought.intialthought}
                </div>
                {" "}
                <hr/>
                <div data-testid="reframed-thought">
                    Reframed Thought: {thought.reframeThought}
                </div>
            </Box>
        </Grid>
    ));

    const [shown, setShown] = React.useState(false);

    const handleClick = () => {
        setShown(!shown);
    };


    const [showTutorialModal, tutorialModalOpenStatus] = React.useState(false);
    const openModal = () => {
        tutorialModalOpenStatus(prev => !prev);
    }

    return (
        <div className={"big-container"}>
            <div className="titleText">
                <h2 data-testid="body-heading">Recently Added Thoughts</h2>
            </div>
            <Container maxWidth="lg">
                <Box
                    sx={{
                        bgcolor: "rgba(220, 191, 250, 0.89)",
                        height: "100vh",
                        borderRadius: "1.5em",
                        maxHeight: "1000px",
                        overflow: "auto",
                        border: "1px solid gray",
                        padding: "20px",
                    }}
                >
                    <Grid container columns={{xs: 4, sm: 8, md: 12}}>
                        <Grid item xs={12} sm={12} md={4}>
                            <Box
                                sx={{
                                    margin: "20px",
                                    height: "200px",
                                    bgcolor: "white",
                                    border: "3px solid gray",
                                    borderRadius: "1.5em",
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center",
                                    fontSize: "1rem",
                                }}
                            >
                                <ThoughtButton handleClick={handleClick}/>
                                {shown ? <ThoughtForm handleClick={handleClick}/> : <></>}
                            </Box>
                            Add A Thought
                        </Grid>
                        {formattedThought}
                    </Grid>
                </Box>
            </Container>
            <Container>
                <Button onClick={openModal}>Tutorial</Button>
                <TutorialModal showTutorialModal={showTutorialModal} tutorialModalOpenStatus={tutorialModalOpenStatus}/>
            </Container>
        </div>
    );
};
