import React from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@material-ui/core';


const SectionOne = ({ handleClick, nextStep, handleChange, values }) => {
  const Continue = e => {
    e.preventDefault();
    nextStep();
  }

  const closeForm = (event, reason) => {
    if (reason && reason === "backdropClick") {
      handleClick();
    }
  }
  return (
    <div>
      <Dialog open onClose={ closeForm }>
        <DialogTitle>Reframe a Thought</DialogTitle>
        <DialogContent>
          <TextField 
            label="What is your negative thought?"
            onChange={handleChange('firstThought')}
            multiline
            minRows={4}
            maxRows={4}
            defaultValue={values.firstThought}
            variant="outlined"
          />
          <br />
          <br />
          <TextField 
            label="Is this negative thought true?"
            onChange={handleChange('firstQuestion')}
            multiline
            minRows={4}
            maxRows={4}
            defaultValue={values.firstQuestion}
            variant="outlined"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={ Continue } aria-label="Next Button">Next</Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
export default SectionOne

