import React from 'react';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from '@material-ui/core';
import cbtService from "../../services/cbtService";

const SectionTwo = ({ handleClick, prevStep, nextStep, handleChange, values }) => {


  const Previous = e => {
    e.preventDefault();
    prevStep();
  }

  const submitForm = async () => {
    await cbtService.saveFirstThought(values)
    handleClick();
    nextStep();
  }

  const closeForm = (event, reason) => {
    if (reason && reason === "backdropClick") {
      handleClick();
    }
  }

  return (
    <div>
      <Dialog open onClose={ closeForm }>
        <DialogTitle>Reframe a Thought</DialogTitle>
        <DialogContent>
          <TextField 
            label="Do I have evidence to support that?"
            onChange={handleChange('secondQuestion')}
            multiline
            minRows={4}
            maxRows={4}
            defaultValue={values.secondQuestion}
            variant="outlined"
          />
          <br />
          <br />
          <TextField 
            label="Is there another way to look at the trigger?"
            onChange={handleChange('reframeThought')}
            multiline
            minRows={4}
            maxRows={4}
            defaultValue={values.reframeThought}
            variant="outlined"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={ Previous }>Back</Button>
          <Button onClick={ submitForm } type="submit">Submit</Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
export default SectionTwo

