import axios from "axios";
export const BASE_URL = "http://localhost:8080";

const saveFirstThought = async (values) => {
    try {
        const config = { headers: {'Content-Type': 'text/plain'} };
        return await axios.post(`${BASE_URL}/api/cbt`,values.firstThought, config);

    } catch (e) {
        console.log("Failed to save First Thought: ",e.message)
        return {};
    }
}
export default {saveFirstThought}
